@import url(https://fonts.googleapis.com/css?family=Ubuntu);
/* VARIABLES */
:root {
  --primary-color: lightgray; 
  --link-color: black;
}

html {
height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  
  background: url(https://cdn.wallpapersafari.com/63/62/qmLtuM.jpg);
  background-position: center;
  background-size: cover;  
}



.navigation {
    background-color: rgb(44, 27, 2);
    box-shadow: 0 10px 20px rgba(32, 32, 32, 0.77);
}

.nav-link {
    padding: 30px;
    -webkit-transition: 500ms;
    transition: 500ms;
}

.nav-link:hover {
    background-color: rgb(112, 185, 154);
    border-radius: 30px;
  }

a {
    color: var(--link-color);
}

.nav-active {
    color: #FF00FF;
    background: green;
}
.mainContent {
    margin-top: 5%;
}
    
h1 {
    padding-bottom: 20px;
}
    
.table thead {
    background: var(--primary-color);
    font-weight: bold;
}

.btn-basic{
    background: var(--primary-color);
    
}

.booklist > h2 {
  font: 400 40px/1.5 Helvetica, Verdana, sans-serif;
  margin: 0;
  padding: 0;
  border-bottom: 3px solid black;
}
 
.booklist > ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
 
.booklist > ul > li {
  font: 200 20px/1.5 Helvetica, Verdana, sans-serif;
  border-bottom: 1px solid #ccc;
}
 
.booklist > ul > li:last-child {
  border: none;
}
 
.booklist > ul > li a {
  text-decoration: none;
  color: #000;
  display: block;
  width: 400px;
 
  -webkit-transition: font-size 0.3s ease, background-color 0.3s ease;
  transition: font-size 0.3s ease, background-color 0.3s ease;
}
 
.booklist > ul > li a:hover {
  font-size: 30px;
  background: #f6f6f6;
}

.bookadd {
	margin-left: auto;
}
    
    
.form-container {
	top: 0;
	height: 100%;
	-webkit-transition: all 0.6s ease-in-out;
	transition: all 0.6s ease-in-out;
	margin-top: 50px;
	margin-left: auto;
	margin-right: auto;
	background-color: rgb(84, 99, 194);	
	border-radius: 10px;
  	box-shadow: 0 14px 28px rgba(0,0,0,0.25), 
				0 10px 10px rgba(0,0,0,0.22);				
	place-items: center center;
	overflow: hidden;
	width: 568px;
	max-width: 100%;
	min-height: 480px;

}

form {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 0 50px;
	height: 50%;
	width: 50%;
	text-align: center;
}

input {
	background-color: rgb(241, 224, 224);
	border-radius: 10px;
	padding: 10px 10px;
	margin: 8px 0;
	outline: none;
	width: 100%;
}


a {
	color: #333;
	font-size: 14px;
	text-decoration: none;
	margin: 15px 0;
}


button {
	border-radius: 20px;
	border: 1px solid #FF4B2B;
	background-color: #FF4B2B;
	color: #FFFFFF;
	font-size: 12px;
	font-weight: bold;
	padding: 12px 45px;
	letter-spacing: 1px;
	outline: none;
	text-transform: uppercase;
	-webkit-transition: -webkit-transform 80ms ease-in;
	transition: -webkit-transform 80ms ease-in;
	transition: transform 80ms ease-in;
	transition: transform 80ms ease-in, -webkit-transform 80ms ease-in;
}

button:hover {
	background-color: rgb(231, 43, 9);
	border: 1px solid rgb(231, 43, 9);
	-moz-osx-font-smoothing: grayscale;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    -webkit-transition: -webkit-transform .25s ease-out;
    transition: -webkit-transform .25s ease-out;
    transition: transform .25s ease-out;
    transition: transform .25s ease-out, -webkit-transform .25s ease-out;
}



